// Update all image src extensions to .webp
hikes.forEach((hike) => {
  hike.images = hike.images.map((img) => {
    const parts = img.src.split(".");
    if (parts.length > 1) {
      parts[parts.length - 1] = "webp";
      return { ...img, src: parts.join(".") };
    }
    return img;
  });
});

export default hikes;
